* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #3db665;
}

.app {
  display: flex;
  flex-direction: row;
  position: relative;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.left-container {
  border: 1px solid black;
  width: 85vh;
  padding: 10px 20px;
  position: relative;
}

.description {
  border: 1px solid black;
  padding: 10px 20px;
  position: relative;
}

.options {
  border: 1px solid black;
  padding: 10px 20px;
  position: relative;
}

.changes {
  border: 1px solid black;
  padding: 10px 20px;
  font-size: larger;
  background-color: #4CAF50;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
  width: 50%;
}


.wheel {
  display: flex;
  flex-direction: row;
  border: 1px solid black;
  width: 86vh;
  position: relative;
}